  height: 200px;
  width: 200px;
  border: 1px solid #333;
  padding: 10px;
  margin: 20px;
  color: #777;
  font-size: 30px;
  z-index: 1;
}
.stageWrapper .box p {
  font-weight: 400;
  margin: 0;
  padding: 0;
}
.stageWrapper .box1 {
  background-color: rgba(143, 188, 143, 0.9);
  z-index: 100;
}
.stageWrapper .box2 {
  background-color: rgba(72, 61, 139, 0.3);
}
.stageWrapper .box3 {
  background-color: rgba(47, 79, 79, 0.9);
  z-index: 100;
}
.stageWrapper .box4 {
  background-color: rgba(148, 0, 211, 0.3);
}
.stageWrapper .level1 {
  box-shadow: 10px 10px 15px 0 rgba(0, 0, 0, 0.8), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  float: left;
}
.stageWrapper .level2 {
  margin: 0px;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.8);
